import { Confirm } from 'notiflix/build/notiflix-confirm-aio';
import { clientPercentage } from '../const';
import { Report } from 'notiflix/build/notiflix-report-aio';

export function onMarginButtonClick() {
  const margin = clientPercentage.tradeMargin;
  Confirm.prompt(
    '–ü—Ä–∏–≤—ñ—Ç',
    '–í–≤–µ–¥–∏ —Ç–æ—Ä–≥–æ–≤—É –Ω–∞—Ü—ñ–Ω–∫—É',
    margin,
    'Answer',
    'Cancel',
    clientAnswer => {
      if (!isNaN(Number(clientAnswer)) && Number(clientAnswer) >= 0) {
        clientPercentage.setTradeMargin(Number(clientAnswer));
        const markup = `–ù–æ–≤–∞ –Ω–∞—Ü—ñ–Ω–∫–∞: ${clientPercentage.tradeMargin}%`;
        Report.success('–£—Å–ø—ñ—à–Ω–æ', markup, 'Okay');
        return;
      }
      Report.failure('–ü–æ–º–∏–ª–∫–∞', '–ù–µ–≤—ñ—Ä–Ω–∏–π —Ñ–æ—Ä–º–∞—Ç', 'Okay');
    },
    clientAnswer => {
      //   Report.failure('–í—ñ–¥–º—ñ–Ω–∞', '', 'Okay');
    },
    {},
  );
}
